import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CargoTypeCode = props => {
  const { handleChange, cargoTypeCode } = props;
  return (
    <TextValidator
      name="cargoTypeCode"
      id="cargoTypeCode"
      required
      onChange={handleChange}
      value={cargoTypeCode}
      label="Cargo Type Code"
      helperText=" UNECE Recommendation 21"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={[
        'required',
        'matchRegexp:^[0-9]$',
        'minNumber:0',
        'maxNumber:9',
      ]}
      errorMessages={[
        '',
        ' 0-9 ',
        ' 1 ',
        ' 1 ',
      ]}
    />
  );
};

export default CargoTypeCode;
