import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const TotalPackageAmount = props => {
  const { handleChange, totalPackageAmount } = props;
  return (
    <TextValidator
      required
      name="totalPackageAmount"
      id="totalPackageAmount"
      onChange={handleChange}
      value={totalPackageAmount}
      label="Total Package Amount"
      helperText=""
      placeholder="1024"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={[
        'required',
        'minNumber:0',
        'maxNumber:99999999',
        'matchRegexp:^[0-9]*$',
      ]}
      errorMessages={[
        '',
        ' 0',
        ' 8 ',
        '',
      ]}
    />
  );
};

export default TotalPackageAmount;
