import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const ReleasePort = props => {
  const { handleChange, releasePort } = props;
  return (
    <TextValidator
      name="releasePort"
      id="releasePort"
      required
      onChange={handleChange}
      value={releasePort}
      label="Release Port"
      helperText=""
      placeholder="0000"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={['required', 'matchRegexp:^[0-9]*$', 'maxStringLength:4']}
      errorMessages={[
        '',
        '',
        ' 4 ',
      ]}
    />
  );
};

export default ReleasePort;
