import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CustomsClearanceID = props => {
  const { handleChange, customsClearanceId } = props;
  return (
    <TextValidator
      name="customsClearanceId"
      id="customsClearanceId"
      required
      onChange={handleChange}
      value={customsClearanceId}
      label="Customs Clearance ID"
      helperText=""
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={[
        'required',
        'matchRegexp:^[A-Za-z0-9]*$',
        'maxStringLength:17',
      ]}
      errorMessages={[
        '',
        '',
        ' 17 ',
      ]}
    />
  );
};

export default CustomsClearanceID;
