import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const ManagerName = props => {
  const { handleChange, managerName } = props;
  return (
    <TextValidator
      name="managerName"
      id="managerName"
      required
      onChange={handleChange}
      value={managerName}
      label="Manager Name"
      helperText=" |  Broker"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={['required', 'maxStringLength:35']}
      errorMessages={['', ' 35 ']}
    />
  );
};

export default ManagerName;
