import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const BrokerBranch = props => {
  const { handleChange, brokerBranch } = props;
  return (
    <TextValidator
      name="brokerBranch"
      id="brokerBranch"
      onChange={handleChange}
      value={brokerBranch}
      label="Broker Branch"
      helperText=" Broker |  0"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={['matchRegexp:^[0-9]*$', 'maxStringLength:6']}
      errorMessages={[' 0-9', ' 6 ']}
    />
  );
};

export default BrokerBranch;
