import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CompanyNameFieldEN = props => {
  const { handleChange, companyNameEN } = props;
  return (
    <TextValidator
      name="companyNameEN"
      id="companyNameEN"
      required
      onChange={handleChange}
      value={companyNameEN}
      label=""
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={[
        'required',
        'matchRegexp:^[^]*$',
        'maxStringLength:70',
      ]}
      errorMessages={[
        '',
        '',
        ' 70 ',
      ]}
    />
  );
};

export default CompanyNameFieldEN;
