import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const BrokerTaxNumber = props => {
  const { handleChange, brokerTaxNumber } = props;
  return (
    <TextValidator
      name="brokerTaxNumber"
      id="brokerTaxNumber"
      onChange={handleChange}
      value={brokerTaxNumber}
      label="Broker Tax Number"
      helperText=" Broker"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={['matchRegexp:^[A-Za-z0-9]*$', 'maxStringLength:17']}
      errorMessages={[
        ' A-Z  0-9',
        ' 17 ',
      ]}
    />
  );
};

export default BrokerTaxNumber;
