import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CompanyTaxNumberField = props => {
  const { handleChange, companyTaxNumber } = props;
  return (
    <TextValidator
      name="companyTaxNumber"
      id="companyTaxNumber"
      required
      onChange={handleChange}
      value={companyTaxNumber}
      label=""
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={[
        'required',
        'matchRegexp:^[A-Za-z0-9]*$',
        'maxStringLength:17',
      ]}
      errorMessages={[
        '',
        ' A-Z  0-9',
        ' 17 ',
      ]}
    />
  );
};

export default CompanyTaxNumberField;
