import React from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';
import { InputAdornment } from '@material-ui/core';
import AdditionalInfoDialog from '../../../shared/additionalInfoDialog';
import ExchangeRateInfo from './infoBody/ExchangeRateInfo';

const ExchangeRate = props => {
  const { handleChange, exchangeRate } = props;
  return (
    <TextValidator
      required
      name="exchangeRate"
      id="exchangeRate"
      onChange={handleChange}
      value={exchangeRate}
      label="Exchange Rate"
      helperText=""
      placeholder="0000.00000"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={['required', 'isFloat', 'isCorrectExchangeRate']}
      errorMessages={[
        '',
        '',
        ' ( i )',
      ]}
      InputProps={{
        endAdornment: (
          <InputAdornment position="end">
            <AdditionalInfoDialog
              titleTH=""
              titleEN="Exchange Rate"
              format="n..12,5"
              maxWidth="xs"
            >
              <ExchangeRateInfo />
            </AdditionalInfoDialog>
          </InputAdornment>
        ),
      }}
    />
  );
};

export default ExchangeRate;
